<template>
    <div class="contentBox orgManage">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/busiOrg/queryOrg" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称：">
                            <el-input placeholder="机构名称" v-model="form.orgName"></el-input>
                        </el-form-item>
                        <el-form-item label="添加时间">
                            <el-date-picker @change="value => form.instDate = value" v-model="form.instDate_" type="date" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="orgName" label="机构名称"> </el-table-column>
                <el-table-column prop="orgIndustryType" label="行业">
                     <template slot-scope="{row}">{{ codeStorage.get('orgIndustryType', row.orgIndustryType) }}</template>
                </el-table-column>
                <el-table-column prop="orgTel" label="电话号码" width="110"> </el-table-column>
                <el-table-column prop="orgAddr" label="机构地址"> </el-table-column>
                <el-table-column prop="bal" label="余额"> </el-table-column>
                <el-table-column prop="custs" label="账户个数" width="80"> </el-table-column>
                <el-table-column prop="updateTime" label="配置时间" width="100">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="{row, index}">
                        <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                        <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-bianji1" @click.stop="editOrg(row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content="关联产品"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-guanlian" @click.stop="correlateProduct(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog title="添加机构" v-model="addOrgFormVisible" @close="closeAddOrgForm">
            <template slot-scope="{close}">
                <add-org-form :close="close"/>
            </template>
        </self-dialog>
        <!-- 编辑dialog -->
        <self-dialog ref="editOrgForm" title="修改机构" v-model="editOrgFormVisible" @close="closeEditOrgForm">
            <template slot-scope="{close, params}">
                <edit-org-form :close="close" :params="params"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addOrgForm from '@admin/page/module/addOrgForm'
import editOrgForm from '@admin/page/module/editOrgForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes("status","orgIndustryType")
@Component
@InjectComponents({ BreadCrumb,addOrgForm,editOrgForm })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    addOrgFormVisible = false
    editOrgFormVisible = false
    queryForm = {}
    // 机构添加
    add() {
        this.addOrgFormVisible =  true
    }
    closeAddOrgForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用
    async changStatus(row){
        const orgId = row.orgId
        const rsp = await this.$helper.fetch("/api/busiOrg/delOrg").setParam("orgId",orgId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
    //编辑
    editOrg(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
            return
        }
        this.editOrgFormVisible =  true
        this.$refs.editOrgForm.setParams(row)
    }
    closeEditOrgForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 关联产品
    async correlateProduct(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
            return
        }
        this.$emit('open',row);
    }
}
</script>
<style scoped>
.orgManage .el-table .el-table__header-wrapper th .cell .el-checkbox{display: none !important}
</style>



