<template>
    <div class="contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/orgBill/queryBillDtl" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称：">
                            <el-input placeholder="机构名称" v-model="form.orgName"></el-input>
                        </el-form-item>
                        <el-form-item label="购买时间">
                            <el-date-picker @change="value => form.busiDate = value" type="date" v-model="form.busiDate_" style="width: 100%;"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <a :href="downloadUrl" type="primary" target="_blank"><el-button type="success">下载</el-button></a>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="orgName" label="机构名称"> </el-table-column>
                <el-table-column prop="userName" label="购买人员"> </el-table-column>
                <el-table-column prop="custName" label="使用客户"> </el-table-column>
                <el-table-column prop="busiDate" label="使用时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.busiDate, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="prodName" label="产品名称" width="180"> </el-table-column>
                <el-table-column prop="freNum" label="产品频次"> </el-table-column>
                <el-table-column prop="billAmt" label="金额"> </el-table-column>
                <el-table-column prop="mngStatus" label="产品状态">
                    <template slot-scope="{row}">{{ codeStorage.get('mngStatus', row.mngStatus) }}</template>
                </el-table-column>
             </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
require('@/store/cache').cacheCodes('userType', "status","mngStatus")
@Component
@InjectComponents({ BreadCrumb })
export default class consumeDetail extends Vue {
    @State(state => state.cache.codeStorage) codeStorage
    @Prop({type: Boolean, required: false, default: true}) autoload
    queryForm = {}
    @Computed downloadUrl() {
        const inStr = encodeURIComponent(JSON.stringify({...this.queryForm}))
        return `/api/orgBill/dowBillDtl?in=${inStr}`
    }
}
</script>



