<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <risk-product-manage ref="riskProduct" @open="open"/>
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
        <template v-if="!couplingIndex">
          <risk-product-add ref="addRiskProductForm" @close="close" :autoload="false"/>
        </template>
        <template v-if="couplingIndex">
          <risk-product-index ref="riskProductIndexForm" @close="close" :autoload="false"/>
        </template>
    </template> 
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import riskProductManage from './riskProductManage'
import riskProductAdd from './riskProductAdd'
import riskProductIndex from './riskProductIndex'
@Component
@InjectComponents({riskProductManage,riskProductAdd,riskProductIndex})
export default class ModifyLoginPwdForm extends Vue{
  visible = true
  couplingIndex = true
  //获取accmanage中的emit传过来的参数
  open(params) { 
    this.visible = false
    const emitFunction = params.emitFunction
    if(emitFunction == 'couplingIndex'){ 
      // console.log(params)
      this.couplingIndex = true
      this.$nextTick(() => {
        this.$refs.riskProductIndexForm.setParams(params)
      })
    }else{
      // console.log('详情')
      this.couplingIndex = false
      this.$nextTick(() => {
        this.$refs.addRiskProductForm.setParams(params)
      })
    }
  }
  close(data) {
      this.visible = true
  }
}
</script>




