<template>
    <div class="contentBox">
        <div class="breadCrumb">
            <el-breadcrumb separator="》">
                <el-breadcrumb-item>产品管理</el-breadcrumb-item>
                <el-breadcrumb-item >风控产品</el-breadcrumb-item>
                <el-breadcrumb-item>产品详情</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="riskProductAdd container whiteBg">
            <div class="halfContainer" style="padding-right:10px">
                <el-form :inline="true" :model="form" class="demo-form-inline" ref="form" :rules="rules">
                    <el-form-item label="产品名称：" prop="prodName">
                        <el-input v-model="form.prodName"></el-input>
                    </el-form-item>
                    <el-form-item label="产品头像："prop="prodLibPath">
                        <el-upload class="avatar-uploader" style="width:102px" action="#" :show-file-list="false" :http-request="httpRequestUpload" v-loading="prodHead">
                            <img v-if="form.prodLibPath" :src="form.prodLibPath" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="产品简介：" prop="prodRemark">
                        <el-input type="textarea" v-model="form.prodRemark"></el-input>
                    </el-form-item>
                     <el-form-item label="费用：" prop="costAmt">
                        <el-input v-model="form.costAmt"></el-input>
                    </el-form-item>
                    <el-form-item label="风控等级：" prop="mngType">
                        <el-select v-model="form.mngType" clearable placeholder="请选择">
                            <el-option v-for="(item, index) in codeStorage.dataSource('mngType')" :key="index" :label="item.label" :value="item.value"/>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="规则链：" prop="ruleId">
                        <el-input v-model="form.ruleId"></el-input>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="评分链：" prop="scoreId">
                        <el-input v-model="form.scoreId"></el-input>
                        </el-select>
                    </el-form-item>
                </el-form>
                 <div class="halfButton">
                     <el-button type="info" @click="() => $emit('close')" >取消</el-button>
                    <el-button type="primary" @click="confirm" :loading="submitLoading">确定</el-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
require('@/store/cache').cacheCodes('userType', "mngType")
@Component
@InjectComponents({ BreadCrumb })
export default class homepage extends Vue {
    @State(state => state.cache.codeStorage) codeStorage
    submitLoading =false
    form = {"prodName":"","prodRemark":"","costAmt":"","mngType":"","ruleId":"","prodId":"","prodLibPath":"","scoreId":""}
    imageUrl = ''
    a = null
    prodHead = false
    rules = {
        prodName: [
            {trigger: "blur", required: true, message: '请输入产品名称'},
            {trigger: 'blur', max: 32, message: '长度不得超过32个字符' },
            {whitespace: true, message: '不能包含空格'},
        ],
        prodRemark: [
             {trigger: "blur", required: true, message: '请输入产品简介'},
            {trigger: 'blur', max: 256, message: '长度不得超过256个字符' },
            {whitespace: true, message: '不能包含空格'},
        ],
        costAmt: [
            {trigger: "blur", required: true, message: '请输入费用'},
            {trigger: 'blur, change', max: 9, message: '长度不得超过9个字符' },
            {trigger: "blur, change", validator: (rule, value, callback) => {
             var isNum=/^\d+(\.\d{0,2})?$/;
            if (isNum.test(value)) return callback()
            return callback(new Error('请输入正确的金额'))
         }}
        ],
        mngType: [
             {trigger: 'change', required: true, message: '请选择风控等级'}
        ],
        ruleId: [
            {trigger: "blur", required: true, message: '请输入规则链'},
            {trigger: 'blur', max: 64, message: '长度不得超过64个字符'},
            {whitespace: true, message: '不能包含空格'},
        ],
        scoreId:[ 
            {trigger: "blur", required: true, message: '请输入评分链'},
            {trigger: 'blur', max: 64, message: '长度不得超过64个字符'},
            {whitespace: true, message: '不能包含空格'},
        ]
    }
    // 上传图片
    async httpRequestUpload({file}) {
        this.prodHead = true
        const rsp = await this.$helper.fetch('/api/prod/uploadFile').setFile('file',file).send()
        // await this.$helper.tools.delay(999999999)
        this.prodHead = false
        if (!rsp.success) return rsp.success
        this.form.prodLibPath = rsp.data.value
        this.$notify({title: '成功',message:  '图片上传成功',type: 'success',duration:4000});
    }
    async confirm() {
        const thisForm = this.form
        const saveOrgObj = {
            tBusiProd:{"prodName":thisForm['prodName'],"prodRemark":thisForm['prodRemark'],"costAmt":thisForm['costAmt'],"mngType":thisForm['mngType'],"ruleId":thisForm['ruleId'],"scoreId":thisForm['scoreId'],"prodId":thisForm['prodId']},
            "prodLibPath":thisForm['prodLibPath']
        }
        if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
        this.submitLoading =true
        const rsp = await this.$helper.fetch('/api/prod/saveOrUpdateProd').setParams(saveOrgObj).send()
        this.submitLoading =false
        if (!rsp.success) return rsp.success
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
        this.$emit('close')
    }
    setParams(params = {}){
        let {costAmt = ''} = params
        costAmt = costAmt.toString()
        const newParams = {...params,costAmt}
        this.form = {...this.form, ...newParams}
    }
}
</script>
<style scoped>
    .avatar-uploader .el-upload {border: 1px dashed #d9d9d9;border-radius: 6px;cursor: pointer;position: relative;overflow: hidden;}
    .avatar-uploader .el-upload:hover {border-color: #409EFF;}
    .avatar-uploader-icon {font-size: 28px;color: #8c939d;width: 100px;height: 100px;line-height: 100px; text-align: center;border: 1px solid #bfcbd9;border-radius: 4px}
    .avatar {width: 100px;height: 100px;display: block;}
    .halfButton .el-button{width: 110px;margin-right: 20px}
    .halfButton{float: right;border-bottom: none;margin-top: 50px}
</style>



