<template>
    <div class="roleManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/prod/queryList" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="产品名称：">
                            <el-input placeholder="产品名称" v-model="form.prodName"></el-input>
                        </el-form-item>
                        <el-form-item label="管理级别">
                            <el-select v-model="form.mngType" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('mngType')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="状态：">
                            <el-select v-model="form.status" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="editProduct()">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="prodId" label="产品ID"width="250"> </el-table-column>
                <el-table-column prop="prodName" label="产品名称"> </el-table-column>
                <el-table-column prop="costAmt" label="费用" width="60"> </el-table-column>
                <el-table-column prop="mngType" label="管理等级" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('mngType', row.mngType) }}</template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="60">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
               <el-table-column prop="userName" label="操作人员" width="80"> </el-table-column>
                <el-table-column prop="insertTime" label="上架时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                    <template slot-scope="{row, index}">
                        <el-tooltip class="item" effect="light" content="关联指标"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-guanlian" @click.stop="couplingIndex(row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-bianji1" @click.stop="editProduct(row)"></el-button>
                        </el-tooltip>
                        <template v-if="row.status == '00001001'">
                            <el-tooltip class="item" effect="light" content="下架"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-xiajia" @click.stop="changStatus(row)"></el-button>
                            </el-tooltip>
                        </template>
                        <template v-if="row.status == '00001002'">
                            <el-tooltip class="item" effect="light" content="上架"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-shangjia" @click.stop="changStatus(row)"></el-button>
                            </el-tooltip>
                        </template>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('userType', "status","mngType")
@Component
@InjectComponents({ BreadCrumb })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    pars ={}
    queryForm = {}
    states = null
    // 上架下架
    async changStatus(row){
         if(row.status =='00001002') await this.$confirm("确定上架产品："+row.prodName+" ?",'提示')
         else{await this.$confirm("确定下架产品："+row.prodName+" ?",'提示')}
        const prodId = row.prodId
        const rsp = await this.$helper.fetch("/api/prod/alterStatus").setParam("prodId",prodId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
    // 关联指标
    async couplingIndex(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '该产品已下架，若需进行当前操作，请上架',type: 'error',duration:4000});
            return
        }
        // if(!row) {row={}}
        row.emitFunction = 'couplingIndex'
        this.$emit('open',row);
    }
    // 新增|编辑
    async editProduct(row){
        if(!row) {row={}}else{
            if(row.status =='00001002'){
                this.$notify({title: '提示',message:  '该产品已下架，若需进行当前操作，请上架',type: 'error',duration:4000});
                return
            }
        }
        this.$emit('open',row);
    }
}
</script>


