<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <role-manage ref="roleManage" @open="open"/>
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
        <role-menu-config ref="roleMenuConfigForm" @close="close" :autoload="false"/>
    </template> 
  </div>
</template>

<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import roleManage from './roleManage'
import roleMenuConfig from './roleMenuConfig'
@Component
@InjectComponents({roleManage,roleMenuConfig})
export default class ModifyLoginPwdForm extends Vue{
  visible = true
  //获取accmanage中的emit传过来的参数
  open(params) { 
    this.visible = false
    this.$nextTick(() => {
      this.$refs.roleMenuConfigForm.setParams(params)
    })
  }
  close(data) {
      this.visible = true
  }
}
</script>




