<template>
  <div class="full login" @keyup.enter="submit2">
    <div class="loginBox full">
      <!-- <img src="../assets/login/background.png" class="loginBg"> -->
      <img src="../assets/login/img_ziti.png" alt="" class="loginLogo" >
      <el-form ref="form" class="loginForm" :model="form" :rules="rules">
        <img src="../assets/login/input_baseboard.png" alt="" class="loginFormBg">
        <div class="loginFormBox">
          <h1>后台登录系统</h1>
          <el-form-item label="用户名：" prop="loginNo">
            <el-input v-model="form.loginNo" auto-complete="off" :maxlength="20" :autofocus="true"></el-input>
          </el-form-item>
          <el-form-item label="密 码：" prop="loginPwd">
            <el-input v-model="form.loginPwd" auto-complete="off" type="password" :maxlength="20"></el-input>
          </el-form-item>
          <!-- <el-form-item prop="loginNo"><el-input placeholder="用户名" :maxlength="20" v-model="form.loginNo" auto-complete="off"/></el-form-item> -->
          <!-- <el-form-item prop="loginPwd"><el-input type="password" placeholder="密码" :maxlength="20" v-model="form.loginPwd" auto-complete="off"/></el-form-item> -->
          <el-form-item class="loginBtn">
            <el-button type="primary" style="width: 100%;" :loading="submitLoading" @click="submit2">登录</el-button>
          </el-form-item>
        </div>
      </el-form> 
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import {Component, InjectComponents, Data, Prop, Watch, Lifecycle, Computed, Filter, State, Action, Mutation} from 'vue-decorators'
@Component
export default class Network extends Vue {
  @State("auth") auth
  @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile
  @Action('cache/run') cacheRun
  submitLoading = false;
  form = { loginNo: '', pwd: '' }
  rules = {
    loginNo: [
      {trigger: "blur", required: true, message: '请输入用户名'},
      {whitespace: true, message: '不能包含空格'},
      {max: 36, message: '长度不能大于36个字符'}
    ],
    loginPwd: [
      {trigger: "blur", required: true, message: '请输入密码'},
      {whitespace: true, message: '不能包含空格'},
      {max: 36, message: '长度不能大于36个字符'}
    ]
  }

  async submit2() { 
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading = true
    const rsp = await this.$helper.fetch('/api/login').setParams({...this.form}).send()
    this.submitLoading = false
    if (!rsp.success) return rsp.success
    const {loginStatus = false, currentUserProfile} = rsp.data
    if (!loginStatus) return this.$notify({title: '提示',message:  '登录失败',type: 'error',duration:4000});

    // 登录成功执行缓存
    await this.cacheRun()
		this.$notify({title: '成功',message:  '登录成功',type: 'success',duration:4000});
    await this.setCurrentUserProfile(currentUserProfile)
    const {currentRouterName = 'index'} = this.auth
    this.$router.replace({name: currentRouterName})
  }
}
</script>
<style scoped>
body{margin: 0}
.login {background: url('../assets/login/img_login.png') center center no-repeat; height: 100%; background-size: 100% 100%;}
.login .loginBg{width: 100%; }
.login .loginBox{position: relative;}
.login .loginForm{position: absolute;top: 60%; left: 50%; margin-left: -260px; margin-top: -213px;width: 520px;}
.login .loginFormBox{position: absolute;top: 0;width: 516px;text-align: center;left: 3px}
.login .loginFormBox .el-form-item{margin-bottom: 22px;margin-left: 40px;}
.login .loginLogo{position: absolute;top: 4%;width: 400px;left: 50%;margin-left: -200px}
.login .loginFormBg{width: 520px;}
/* .loginBox .loginFormBg { width: 360px; padding: 50px 70px; background-image: url('../assets/login/baseboard.png'); border-radius: 16px 16px; position: absolute; } */
.loginBox .loginForm h1 { line-height: 92px; font-size: 30px;font-weight: normal;text-indent: 40px;text-align: left; color: #50c7cd;margin: 0;margin-bottom: 22px;}
.loginBox .el-form-item__content input{line-height: 42px}
.el-input, .el-input__inner{height: 42px;}
.loginBox .el-form-item__content button{line-height: 40px}
.login .el-button--primary{background-color: #50c7cd;border: none;padding: 0;margin-top: 70px;}
.loginBtn{ position: absolute; right: 40px;bottom: 3px;width: 100px !important;margin-left: 0;margin-right: 60px}
.loginBtn button{width: 100px}
.login .el-input{float: left}
.login .el-input .el-input__inner{height: 40px !important;line-height: 40px}
/* .el-form-item__label{width: 100px} */
/* .el-input .el-input__inner{width: 200px} */
/* .loginBox .loginCaptcha { width: 100%; height:100%; border-radius: 4px 4px; cursor: pointer;} */
</style>


