<template>
    <div class="roleManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/role/queryRoles" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="角色：">
                            <el-input placeholder="角色" v-model="form.roleName"></el-input>
                        </el-form-item>
                        <el-form-item label="状态">
                            <el-select v-model="form.status" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="roleName" label="角色"> </el-table-column>
                <el-table-column prop="roleType" label="类型">
                    <template slot-scope="{row}">{{ codeStorage.get('roleType', row.roleType) }}</template>
                </el-table-column>
                <el-table-column prop="insertTime" label="添加时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                    <template slot-scope="{row, index}">
                        <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                        <el-tooltip class="item" effect="light" content="配置菜单"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-peizhi" @click.stop="roleMenuConfig(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog title="添加角色" v-model="addRoleFormVisible" @close="closeaddRoleForm">
            <template slot-scope="{close}">
                <add-role-form :close="close"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addRoleForm from '@admin/page/module/addRoleForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('userType', "status","roleType")
@Component
@InjectComponents({ BreadCrumb,addRoleForm })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    addRoleFormVisible = false
    pars ={}
    queryForm = {}
    states = null
    // 机构添加
    add() {
        this.addRoleFormVisible =  true
    }
    closeaddRoleForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用
    async changStatus(row){
        const roleId = row.roleId
        const rsp = await this.$helper.fetch("/api/role/deleteRole").setParam("roleId",roleId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
     // 配置菜单
    async roleMenuConfig(row){
        if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
          return
    }
        this.$emit('open',row);
    }
}
</script>


