<template>
    <div class="contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/log/getOpLogLists" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称:">
                            <el-input v-model="form.orgName" placeholder="机构名称"></el-input>
                        </el-form-item>
                        <el-form-item label="操作人员:">
                            <el-input v-model="form.sBody" placeholder="操作人员"></el-input>
                        </el-form-item>
                        <el-form-item label="执行时间:">
                           <el-date-picker @change="value => form.instDate = value" v-model="form.instDate_" type="date" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="执行动作:">
                            <el-select v-model="form.busiOpType" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('busiOpType')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="orgName" label="机构名称"> </el-table-column>
                <el-table-column prop="userName" label="操作人员"> </el-table-column>
                <el-table-column prop="custName" label="客户姓名"> </el-table-column>
                  <el-table-column prop="prodName" label="产品名称"> </el-table-column>
                <el-table-column prop="insertTime" label="执行时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD hh:ss")}}</template>
                </el-table-column>
            
                <el-table-column prop="busiOpType" label="执行动作">
                    <template slot-scope="{row}">{{ codeStorage.get('busiOpType', row.busiOpType) }}</template>
                </el-table-column>
              
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes("logState",'busiOpType')
@Component
@InjectComponents({ BreadCrumb })
export default class log extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    queryForm = {}
}
</script>


