<template>
    <div class="contentBox coupleProdIndex">
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/prod/getAllIndicator" v-model="queryForm" :params="() => ({'prodId': form.prodId})">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="指标名称" class="codeNammeInput">
                            <el-input v-model="form.indicName" placeholder="指标名称"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="indicatorName" label="指标名称"> </el-table-column>
                <el-table-column prop="indicatorId" label="code编码"></el-table-column>
                <el-table-column prop="indicatorType" label="指标类型" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('indicatorType', row.indicatorType) }}</template>
                </el-table-column>
                 <el-table-column prop="indcType" label="业务类型" width="100">
                    <template slot-scope="{row}">{{ codeStorage.get('indcType', row.indcType) }}</template>
                </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('userType', "indicatorType","indcType")
@Component
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
    @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
    @Prop({type: Boolean, required: false, default: true}) autoload
    queryForm = {}
    form = {'prodId':''}
    // 添加
    async add(){
        const rows = this.$refs.grid.selectedRows
        if(rows.length ==0) 
        return this.$notify({message:  '请至少选择一条数据',type: 'error',duration:4000});
        const setIndicatorIds = []
        for(var i in rows){setIndicatorIds.push(rows[i].indicatorId)}
        const setObj = {"indicatorIds":setIndicatorIds,"prodId":this.form.prodId}
        const rsp = await this.$helper.fetch("/api/prod/doIndicatorProdRel").setParams(setObj).send()
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
        this.close()
    }
    @Lifecycle created() {
        this.form = {...this.form, ...this.params}
    }
}
</script>



