<template>
    <div class="contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/log/getLogLists" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称:">
                            <el-input v-model="form.orgName" placeholder="机构名称"></el-input>
                        </el-form-item>
                        <el-form-item label="产品名称:">
                            <el-input v-model="form.prodName" placeholder="产品名称"></el-input>
                        </el-form-item>
                        <el-form-item label="执行时间:">
                           <el-date-picker @change="value => form.instDate = value" v-model="form.instDate_" type="date" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="执行结果:">
                            <el-select v-model="form.logState" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('logState')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="orgName" label="机构名称"> </el-table-column>
                <el-table-column prop="userName" label="购买人员"> </el-table-column>
                <el-table-column prop="custName" label="使用客户"> </el-table-column>
                <el-table-column prop="insertTime" label="执行时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="prodName" label="产品名称"> </el-table-column>
                <el-table-column prop="ruleId" label="产品规则链"> </el-table-column>
                <el-table-column prop="logState" label="执行结果">
                    <template slot-scope="{row}">{{ codeStorage.get('logState', row.logState) }}</template>
                </el-table-column>
                <el-table-column prop="errorReason" label="原因"> </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes("logState")
@Component
@InjectComponents({ BreadCrumb })
export default class log extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    queryForm = {}
}
</script>


