<template>
    <div class="codeManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
            <div class="halfContainer halfSearch" style="padding-right:10px">
                <div class="halfTitle">码类明细</div>
                <self-grid ref="grid" url="/api/code/queryCodeType" v-model="queryForm1" :multi="false" :checkBox="false" @rowclick="rowClick">
                    <template slot="query" slot-scope="{form, query, reset}">
                        <el-form :inline="true" :model="form" class="demo-form-inline">
                            <el-form-item label="码类名称：">
                                <el-input placeholder="码类名称" v-model="form.codeTypeName"></el-input>
                            </el-form-item>
                            <el-form-item class="buttonTeam">
                                <el-button type="primary" @click="addCodeType">添加</el-button>
                                <el-button type="warning" @click="query">查询</el-button>
                            </el-form-item>
                        </el-form>
                    </template>
                    <el-table-column prop="codeTypeId" label="码类编码"> </el-table-column>
                    <el-table-column prop="codeTypeName" label="码类名称"> </el-table-column>
                    <el-table-column prop="status" label="码类状态" width="80">
                        <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                    </el-table-column>
                    <el-table-column label="操作" width="150">
                        <template slot-scope="{row, index}">
                            <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changCodeTypeStatus(row)"></el-switch>
                            <el-tooltip class="item" effect="light" content="修改"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" @click.stop="editCodeType(row)" class="iconfont icon-bianji1"></el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </self-grid>
            </div>
            <!-- 添加码类dialog -->
            <self-dialog title="添加码类" v-model="addCodeTypeFormVisible" @close="closeAddCodeTypeForm">
                <template slot-scope="{close}">
                    <add-code-type-form :close="close"/>
                </template>
            </self-dialog>
            <!-- 添加码类dialog -->
            <self-dialog ref="editCodeTypeForm" title="修改码类" v-model="editCodeTypeFormVisible" @close="closeEdiCodeTypeForm">
                <template slot-scope="{close, params}">
                    <edit-code-type-form :close="close" :params="params"/>
                </template>
            </self-dialog>
            <div class="halfContainer" style="padding-left:10px">
                <div class="halfTitle">码值明细</div>
               <self-grid ref="grid2" url="/api/code/queryCodeValue" v-model="queryForm2" :multi="false" :checkBox="false">
                    <template slot="query" slot-scope="{form, query, reset}">
                        <el-form :inline="true" :model="form" class="demo-form-inline">
                            <el-form-item class="buttonTeam">
                                <el-button type="primary" @click="addCodeValue">添加</el-button>
                            </el-form-item>
                        </el-form>
                    </template>
                    <el-table-column prop="codeInfoValue" label="码值编码"> </el-table-column>
                    <el-table-column prop="codeInfoName" label="码值名称"> </el-table-column>
                    <el-table-column prop="status" label="码值状态" width="80">
                        <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                    </el-table-column>
                    <el-table-column label="操作" width="150">
                        <template slot-scope="{row, index}">
                            <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changCodeValueStatus(row)"></el-switch>
                            <el-tooltip class="item" effect="light" content="修改"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" @click.stop="editCodeValue(row)" class="iconfont icon-bianji1"></el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </self-grid>
            </div>
            <!-- 添加码值dialog -->
            <self-dialog ref="addCodeValueForm" title="添加码值" v-model="addCodeValueFormVisible" @close="closeAddCodeValueForm">
                <template slot-scope="{close, params}">
                    <add-code-value-form :close="close" :params="params"/>
                </template>
            </self-dialog>
            <!-- 添加码值dialog -->
            <self-dialog ref="editCodeValueForm" title="修改码值" v-model="editCodeValueFormVisible" @close="closeEdiCodeValueForm">
                <template slot-scope="{close, params}">
                    <edit-code-value-form :close="close" :params="params"/>
                </template>
            </self-dialog>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addCodeTypeForm from '@admin/page/module/addCodeTypeForm'
import editCodeTypeForm from '@admin/page/module/editCodeTypeForm'
import addCodeValueForm from '@admin/page/module/addCodeValueForm'
import editCodeValueForm from '@admin/page/module/editCodeValueForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
//码表
require('@/store/cache').cacheCodes("status")
@Component
@InjectComponents({ BreadCrumb,addCodeTypeForm,editCodeTypeForm,addCodeValueForm,editCodeValueForm })
export default class codeManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    addCodeTypeFormVisible = false
    editCodeTypeFormVisible = false
    addCodeValueFormVisible = false
    editCodeValueFormVisible = false
    queryForm1 = {}
    queryForm2 = {}
    chooseCodeTypeId = ''
    // 添加码类
    addCodeType() {
        this.addCodeTypeFormVisible =  true
    }
    closeAddCodeTypeForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用码类
    async changCodeTypeStatus(row){
        const codeTypeId = row.codeTypeId
        const rsp = await this.$helper.fetch("/api/code/updateCodeTypeStatus").setParam("codeTypeId",codeTypeId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
    // 编辑码类
    editCodeType(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
            return
        }
        this.editCodeTypeFormVisible =  true
        this.$refs.editCodeTypeForm.setParams(row)
    }
    closeEdiCodeTypeForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
    }
    // 选择码类后更新邮编码值
    rowClick({codeTypeId}) {
        this.$refs.grid2.setDefaultParams({codeTypeId}).load({"pageNo":1})
        this.chooseCodeTypeId = codeTypeId
    }
    // 添加码值
    addCodeValue() {
        if(!this.chooseCodeTypeId) 
        return this.$notify({message:  '请选择需要添加的码类',type: 'error',duration:4000});
        const codeInfoRow = {'codeTypeId': this.chooseCodeTypeId}
        this.addCodeValueFormVisible =  true
        this.$refs.addCodeValueForm.setParams(codeInfoRow)
    }
    closeAddCodeValueForm(closed = false, ...args){
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用码值
    async changCodeValueStatus(row){
        console.log(row)
        const codeInfoId = row.codeInfoValue
        const rsp = await this.$helper.fetch("/api/code/updateCodeInfoStatus").setParam("codeInfoId",codeInfoId).send()
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
    //编辑码值
    editCodeValue(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
            return
        }
        this.editCodeValueFormVisible =  true
        this.$refs.editCodeValueForm.setParams(row)
    }
    closeEdiCodeValueForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$refs.grid2.load({"pageNo":1});//重新加载列表
    }
  
}
</script>
<style scoped>
.codeManage .el-table th > .cell {width: 100px;}
</style>


