<template>
    <div class="roleManage contentBox">
        <div class="breadCrumb">
            <el-breadcrumb separator="》">
                <el-breadcrumb-item>客户管理</el-breadcrumb-item>
                <el-breadcrumb-item >机构管理</el-breadcrumb-item>
                <el-breadcrumb-item>关联产品</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/busiOrg/queryOrgYesProd" v-model="queryForm" :multi="false" :autoload="autoload">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="产品名称：">
                            <el-input placeholder="产品名称" v-model="form.prodName"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="danger" @click="couple(form)">关联</el-button>
                            <el-button type="info" @click="() => $emit('close')" >返回</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="prodId" label="产品ID"> </el-table-column>
                <el-table-column prop="prodName" label="产品名称"> </el-table-column>
                <el-table-column prop="costAmt" label="费用"> </el-table-column>
                <el-table-column prop="mngType" label="风控等级">
                    <template slot-scope="{row}">{{ codeStorage.get('mngType', row.mngType) }}</template>
                </el-table-column>
                <el-table-column prop="updateTime" label="上架时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.updateTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="ralstatus" label="状态" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.ralstatus) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="80">
                    <template slot-scope="{row}" v-if="row.ralstatus != '00001001'">
                        <el-tooltip class="item" effect="light" content="删除"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-shanchu" @click.stop="del(row)" disabled></el-button>
                        </el-tooltip>
                    </template>
                    <template slot-scope="{row}" v-if="row.ralstatus != '00001002'">
                        <el-tooltip class="item" effect="light" content="删除"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-shanchu" @click.stop="del(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog ref="coupleOrgProductForm" title="风控产品添加" v-model="coupleOrgProductFormVisible" @close="closeCoupleOrgProductForm" class="indexDialog">
            <template slot-scope="{close, params}">
                <couple-org-product-form :close="close" :params="params"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import coupleOrgProductForm from '@admin/page/module/coupleOrgProductForm'
import editOrgForm from '@admin/page/module/editOrgForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('userType', "status","orgIndustryType","mngType","ralstatus")
@Component
@InjectComponents({ BreadCrumb,coupleOrgProductForm,editOrgForm })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
     @Prop({type: Boolean, required: false, default: true}) autoload
    coupleOrgProductFormVisible = false
    editOrgFormVisible = false
    pars ={}
    queryForm = {}
    states = null
    pickerOptions0 = []
    // 关联
    couple(params) {
        this.coupleOrgProductFormVisible =  true
        this.$refs.coupleOrgProductForm.setParams(params)
    }
    closeCoupleOrgProductForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    async del(rows) {
        const orgId = this.queryForm.orgId 
        const dataRows = {"orgId":this.queryForm.orgId,"prodId":rows.prodId}
        const rsp = await this.$helper.fetch('/api/busiOrg/delOrgProd').setParams({...dataRows}).send()
        if (!rsp.success) return
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
    setParams(params = {}){
        const {orgId = ''} = params
        this.queryForm = {...this.queryForm,orgId}
        this.$refs.grid.setDefaultParams({orgId}).load()//内置一个值
    }
}
</script>


