<template>
    <div class="roleManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/orgBill/queryBill" v-model="queryForm" :multi="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称：">
                            <el-input placeholder="机构名称" v-model="form.orgName"></el-input>
                        </el-form-item>
                         <el-form-item label="机构电话：">
                            <el-input placeholder="机构电话" v-model="form.orgTel"></el-input>
                        </el-form-item>
                         <el-form-item label="订单状态：">
                             <el-select v-model="form.status" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="充值时间">
                            <el-date-picker @change="value => form.insertTime = value" v-model="form.insertTime_" type="date" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="orgRecharge">充值</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="billId" label="订单ID" width="270"> </el-table-column>
                <el-table-column prop="orgTel" label="机构电话" width="110"></el-table-column>
                <el-table-column prop="orgName" label="机构名称"> </el-table-column>
                <el-table-column prop="billAmt" label="充值金额"> </el-table-column>
                <el-table-column prop="billType" label="类型">
                    <template slot-scope="{row}">{{ codeStorage.get('billType', row.billType) }}</template>
                </el-table-column>
                <el-table-column prop="insertTime" label="充值时间" width="130">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="订单状态">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="80">
                    <template slot-scope="{row, index}">
                        <el-switch :loading="switchLoading" on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog title="机构充值" v-model="orgRechargeFormVisible" @close="closeOrgRechargeForm">
            <template slot-scope="{close}">
                <org-recharge-form :close="close"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import orgRechargeForm from '@admin/page/module/orgRechargeForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('userType', "status","orgIndustryType","billType")
@Component
@InjectComponents({ BreadCrumb,orgRechargeForm })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    orgRechargeFormVisible = false
    pars ={}
    queryForm = {}
    states = null
    pickerOptions0 = ''
    switchLoading = false
    // 机构充值
    orgRecharge() {
        this.orgRechargeFormVisible =  true
    }
    closeOrgRechargeForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用
    async changStatus(row){
        const billId = row.billId
        this.switchLoading = true
        const rsp = await this.$helper.fetch("/api/orgBill/alterBillStatus").setParam("billId",billId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.switchLoading = false
        if (!rsp.success) return
        // this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
}
</script>


