<template>
    <div class="codeManage contentBox">
        <div class="breadCrumb">
            <el-breadcrumb separator="》">
                <el-breadcrumb-item>系统管理</el-breadcrumb-item>
                <el-breadcrumb-item >角色管理</el-breadcrumb-item>
                <el-breadcrumb-item>配置菜单</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container whiteBg">
            <div class="halfContainer" style="padding-right:10px">
                <el-tree ref="tree" :default-checked-keys="hadMenu" :data="allMenu" node-key="menuId" show-checkbox default-expand-all :props="{label: 'menuName'}"></el-tree>
                <div class="halfButton">
                    <el-button type="info" @click="() => $emit('close')" >取消</el-button>
                    <el-button type="primary" @click="confirm" :loading="confirmLoading">确定</el-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
@Component
@InjectComponents({ BreadCrumb })
export default class homepage extends Vue {
    allMenu=[]
    roleId = ''
    roleType = ''
    hadMenu = []
    confirmLoading = false
    // 查询指标列表
    async queryMenus(){
        const roleTree = {
            "roleId": this.roleId,
            "roleType": this.roleType
        }
        const rsp = await this.$helper.fetch("/api/role/queryRoleHasMenuId").setParams(roleTree).send()
        if (!rsp.success) return
        this.hadMenu = rsp.data.roleMenus
        this.allMenu = this.$helper.tools.toTreeArray(rsp.data.allMenus, 'menuId', 'parentMenuId')
    }
    // 确定修改菜单配置
    async confirm(){
        const nodes = this.$refs.tree.getCheckedNodes()
        const newMenuIds = []
        for(var i in nodes){ newMenuIds.push(nodes[i].menuId) }
        const saveRoleMenuObj = { "menuIds": newMenuIds, "roleId": this.roleId }
        this.confirmLoading = true
        const rsp = await this.$helper.fetch("/api/role/saveRoleMenuId").setParams(saveRoleMenuObj).send()
        this.confirmLoading = false
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
        this.queryMenus()
        this.$emit('close')
    }   
    setParams(params = {}){
        this.roleId = params.roleId
        this.roleType = params.roleType
        this.queryMenus()
    }
}
</script>
<style scoped>
.halfButton .el-button{width: 110px;margin-right: 20px}
.halfButton{float: right;border-bottom: none;margin-top: 50px}
</style>


