<template>
    <div class="menuManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
            <div class="halfContainer" style="padding-right:10px">
                <div class="halfTitle">系统菜单</div>
                <div class="halfButton">
                    <!-- <el-button type="danger" @click="() => this.ChangeMenuState('off')">禁用</el-button>
                    <el-button type="warning" @click="() => this.ChangeMenuState('on')">启用</el-button> -->
                    <el-button type="success" @click="addRootIndex">添加根菜单</el-button>
                    <el-button type="primary" @click="addSonIndex">添加下级菜单</el-button>
                </div>
                <el-tree ref="tree" :expand-on-click-node="false" :data="allMenu" node-key="menuId" highlight-current show-checkbox :accordion="true" :props="{label: showTreeLabel}" @node-click="nodeClick"></el-tree>
            </div>
            <div class="halfContainer" style="padding-left:10px">
                <div class="halfTitle">菜单明细</div>
                <el-form :inline="true" :model="form" class="demo-form-inline" ref="form" :rules="rules">
                    <el-form-item label="菜单编号：" prop="menuId">
                        <el-input placeholder="菜单编号" v-model="form.menuId" :disabled="disableControl"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单类型：" prop="menuType">
                        <el-select v-model="form.menuType" clearable placeholder="请选择" :disabled="disableControl">
                            <el-option v-for="(item, index) in codeStorage.dataSource('menuType')" :key="index" :label="item.label" :value="item.value"/>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="菜单名称：" prop="menuName">
                        <el-input placeholder="菜单名称" v-model="form.menuName" :disabled="disableControl"></el-input>
                    </el-form-item>
                     <el-form-item label="菜单连接：">
                        <el-input placeholder="菜单连接" v-model="form.menuUrl" :disabled="disableControl"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单状态："prop="status">
                        <el-select v-model="form.status" clearable placeholder="请选择" :disabled="disableControl">
                            <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="行为类型："prop="actionType">
                        <el-select v-model="form.actionType" clearable placeholder="请选择" :disabled="disableControl">
                            <el-option v-for="(item, index) in codeStorage.dataSource('actionType')" :key="index" :label="item.label" :value="item.value"/>
                        </el-select>
                    </el-form-item>
                     <el-form-item label="授权代码：">
                        <el-input placeholder="授权代码" v-model="form.authCode" :disabled="disableControl"></el-input>
                    </el-form-item>
                     <el-form-item label="菜单序号：">
                        <el-input placeholder="菜单序号" v-model="form.menuOrder" type="number" :disabled="disableControl"></el-input>
                    </el-form-item>
                    <el-form-item class="buttonTeam">
                        <el-button type="primary" @click="submit" :loading="submitLoading" :disabled="disableControl">保存</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div style="clear:both"></div>
    </div>
</template>
<script>
import Vue from "vue";
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
//码表
require('@/store/cache').cacheCodes('menuType', "status","actionType")
@Component
@InjectComponents({ BreadCrumb })
export default class homepage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    @Lifecycle created() {
        this.queryMenus()
    }
    submitLoading =false
    allMenu=[]
    form = {}
    clickType = ''
    setParentId = ''
    addNewMenu = false
    disableControl = true
    // 增加禁用和启动状态显示
    showTreeLabel(data, node, ...args) {
        if (!data.status) return data.label
        return `${data.menuName} [${'00001001' == data.status ? '启': '禁'}]`
    }
    // 查询菜单列表
    async queryMenus(){
        const rsp = await this.$helper.fetch("/api/menu/queryMenus").send()
        if (!rsp.success) return
        if (rsp.data.allMenu.length == 0) return
        // const backMenusDs = this.$helper.tools.toTreeArray(rsp.data.backMenu, 'menuId', 'parentMenuId')
        // const beforMenusDs = this.$helper.tools.toTreeArray(rsp.data.beforMenu, 'menuId', 'parentMenuId')
        const allMenusDs = this.$helper.tools.toTreeArray(rsp.data.allMenu, 'menuId', 'parentMenuId')
        // this.allMenu = [
        //     { menuId:'backMenu', label: '后台系统', children: backMenusDs },
        //     { menuId:'beforMenu', label: '看家狗系统', children: beforMenusDs}
        // ]
        this.allMenu = allMenusDs
    }
    // // 启用禁用
    // async ChangeMenuState(state = 'on'){
    //     const isOn = 'on' == state
    //     const nodes = this.$refs.tree.getCheckedNodes().filter(({status: menuState}) => isOn ? menuState == '00001002' : menuState == '00001001')
    //     if(nodes.length == 0){
            
    //         return this.$notify({message: '请至少选择一项有效菜单',type: 'warning',duration:4000});}
    //     const menuId = nodes.map(({menuId}) => menuId)
    //     const closeMenu = { menuId, type : state}
    //     isOn ? await this.$confirm('此操作将启用该菜单, 是否继续?', '禁用菜单')
    //         : await this.$confirm('此操作将禁用该菜单, 是否继续?', '禁用菜单')
    //     const rsp = await this.$helper.fetch("/api/menu/deleteMenu").setParams(closeMenu).send()
    //     if (!rsp.success) return
    //     this.$notify({message: rsp.message,type: 'success',duration:4000});
    //     this.$refs.grid.load({"pageNo":1});//重新加载列表
    //     this.queryMenus()
    // }
    rules = {
        menuName: [
            {trigger: "blur", required: true, message: '请输入菜单名称'},
            {trigger: 'blur', max: 16, message: '长度不得超过16个字符' },
            {whitespace: true, message: '不能包含空格'}
        ],
        authCode: [
            {trigger: "blur", required: true, message: '请输入授权代码'},
            {trigger: 'blur', max: 30, message: '长度不得超过30个字符' },
            {whitespace: true, message: '不能包含空格'}
        ],
        menuOrder: [
            {trigger: "blur", required: true, message: '请输入菜单序号'},
            {whitespace: true, message: '不能包含空格'},
            {max: 11, message: '长度不能大于11个字符'}
        ],
        status: [
           {trigger: 'change', required: true, message: '请选择状态'}
        ],
        actionType: [
           {trigger: 'change', required: true, message: '请选择行为类型'}
        ],
        menuType: [
           {trigger: 'change', required: true, message: '请选择菜单类型'}
        ],
        menuUrl: [
            {trigger: "blur", required: true, message: '请输入菜单连接'},
            {whitespace: true, message: '不能包含空格'},
             {trigger: 'blur', max: 64, message: '长度不得超过64个字符'}
        ],
        menuId: [
            {trigger: "blur", required: true, message: '请输入菜单编号'},
            {trigger: 'blur', max: 36, message: '长度不得超过36个字符'},
            {whitespace: true, message: '不能包含空格'}
        ]
    }
    // 将左边菜单详情置空
    async restMenu(){
        this.disableControl = false
        this.form = {}
    }
    // 点击选中左边菜单
    nodeClick(data, node, $node) {
        this.form = {...data}
        this.disableControl = false
        this.setParentId = this.form.menuId
    }
    // 添加根指标
    async addRootIndex(){
        this.restMenu()
        this.clickType = 'root'
        this.form.parentMenuId = ''
        this.addNewMenu = true
    }
    // 添加下级指标
    async addSonIndex(){
        // console.log(this.form.parentMenuId)
        if( !this.setParentId || this.setParentId == '') 
        return this.$notify({message: '请选择一个菜单',type: 'warning',duration:4000});
        this.restMenu()
        this.clickType = 'son'
        this.form.parentMenuId = this.setParentId
        this.addNewMenu = true
    }
    // 保存修改、新增
    async submit(){
        // 添加菜单
        const thisForm = this.form
        thisForm.type = this.clickType
        const saveMenuObj = {
            sysMenu:{"menuName":thisForm['menuName'],"authCode":thisForm['authCode'],"menuIcon":thisForm['menuIcon'],"menuOrder":thisForm['menuOrder'],"status":thisForm['status'],"parentMenuId":thisForm['parentMenuId'],"menuUrl":thisForm['menuUrl'],"menuId":thisForm['menuId'],"menuType":thisForm['menuType'],"actionType":thisForm['actionType']},
            "type": thisForm['type']
        }
        const updateMenuObj = {
            sysMenu:{"menuName":thisForm['menuName'],"authCode":thisForm['authCode'],"menuIcon":thisForm['menuIcon'],"menuOrder":thisForm['menuOrder'],"status":thisForm['status'],"parentMenuId":thisForm['parentMenuId'],"menuUrl":thisForm['menuUrl'],"menuId":thisForm['menuId'],"menuType":thisForm['menuType'],"actionType":thisForm['actionType']}
        }
        if(!this.addNewMenu){
            // 编辑菜单
            if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//验证
            this.submitLoading =true
            const rsp = await this.$helper.fetch("/api/menu/updateMenu").setParams(updateMenuObj).send()
            this.submitLoading =false
            if (!rsp.success) return
            this.queryMenus()
            this.$notify({title: '成功',message:  '编辑成功',type: 'success',duration:4000});
            //this.$refs.grid.load({"pageNo":1});//重新加载列表
            
        }else{
            // 新增菜单
            if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//验证
            this.submitLoading =true
            const rsp = await this.$helper.fetch("/api/menu/saveMenu").setParams(saveMenuObj).send()
            this.submitLoading =false
            if (!rsp.success) return
            this.queryMenus()
            this.$notify({title: '成功',message:  '添加成功',type: 'success',duration:4000});
           // this.$refs.grid.load({"pageNo":1});//重新加载列表
        }
    }
}
</script>



