<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="角色:" prop="roleName">
        <el-input v-model="form.roleName" auto-complete="off" required></el-input>
    </el-form-item>
    <el-form-item label="类型:" prop="roleType">
        <el-select v-model="form.roleType" clearable placeholder="请选择">
            <el-option required v-for="(item, index) in codeStorage.dataSource('roleType')" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
require('@/store/cache').cacheCodes('userType', "roleType")
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @Prop({type: Function, required: true, default: null}) close
  @State(state => state.cache.codeStorage) codeStorage
  submitLoading =false
  form = {roleName: '', roleType: ''}
  rules = {
    roleName: [
      {trigger: "blur", required: true, message: '请输入角色'},
      {trigger: 'blur', max: 16, message: '长度不得超过16个字符' },
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/^[\u4e00-\u9fa5]+$/
          if (regex.test(value)) return callback()
          return callback(new Error(`角色名称只能包含汉字`))
      }}
    ],
    roleType: [
      { type: 'string', trigger: 'change', required: true, message: '请选择类型'}
    ],
  }
  async submit() {
    const thisForm = this.form
    const saveOrgObj = {
      "roleName":thisForm['roleName'],
      "roleType":thisForm['roleType']
    }
    console.log(saveOrgObj)
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading =true
    const rsp = await this.$helper.fetch('/api/role/saveRole').setParams(saveOrgObj).send()
    this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '成功',message:  '添加成功',type: 'success',duration:4000});

    this.close()
  }
}
</script>




