<template>
    <div class="contentBox accManage">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/busiCust/queryCusts" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="机构名称：">
                            <el-input placeholder="机构名称" v-model="form.orgName"></el-input>
                        </el-form-item>
                        <el-form-item label="姓名：">
                            <el-input placeholder="姓名" v-model="form.custName"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="loginId" label="账号"> </el-table-column>
                <el-table-column prop="orgName" label="机构名称"></el-table-column>
                <el-table-column prop="roleName" label="角色"> </el-table-column>
                <el-table-column prop="userName" label="姓名" width="80"> </el-table-column>
                <!-- <el-table-column prop="userTel" label="电话号码"> </el-table-column> -->
                <el-table-column prop="userDept" label="部门"> </el-table-column>
                <el-table-column prop="instUserName" label="操作人员"> </el-table-column>
                <el-table-column prop="loginTime" label="上次登录时间">
                     <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.loginTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                    <template slot-scope="{row, index}">
                        <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                        <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" @click.stop="editAcct(row)" class="iconfont icon-bianji1"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content="重置密码"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-zhongzhimima" @click.stop="resetSysUserPwd(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog title="添加账户" v-model="addAcctFormVisible" @close="closeAddAcctForm">
            <template slot-scope="{close}">
                <add-acct-form :close="close"/>
            </template>
        </self-dialog>
        <!-- 编辑dialog -->
        <self-dialog ref="editAcctForm" title="修改账户" v-model="editAcctFormVisible" @close="closeEditAcctForm">
            <template slot-scope="{close, params}">
                <edit-acct-form :close="close" :params="params"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addAcctForm from '@admin/page/module/addAcctForm'
import editAcctForm from '@admin/page/module/editAcctForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes("status")
@Component
@InjectComponents({ BreadCrumb,addAcctForm,editAcctForm })
export default class accManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    addAcctFormVisible = false
    editAcctFormVisible = false
    queryForm = {}
    // 机构添加
    add() {
        this.addAcctFormVisible =  true
    }
    closeAddAcctForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用
    async changStatus(row){
        const newForm = {"userId":row.userId}
        const rsp = await this.$helper.fetch("api/busiCust/delCust").setParams(newForm).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '提示',message:  rsp.message,type: 'success',duration:4000});
    }
    //编辑
    editAcct(row){
          if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
          return
    }
        this.editAcctFormVisible =  true
        this.$refs.editAcctForm.setParams(row)
    }
    closeEditAcctForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    //重置密码
    async resetSysUserPwd(row){
          if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
          return
        }
        await this.$confirm('此操作将重置该用户密码, 是否继续?', '重置密码')
        const rsp = await this.$helper.fetch("/api/busiCust/restPwd").setParam("userId",row.userId).send()
        if (!rsp.success) return
        await this.$alert('初始密码为123456', '重置密码成功')
        this.$refs.grid.load({"pageNo":1});//重新加载列表
       // this.$notify({title: '提示',message:  rsp.message,type: 'success',duration:4000});
    }
}
</script>


