import Vue from 'vue'
import Router from 'vue-router'
import beforeEach from '@/router/beforeEach'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    { path: '/login', name: 'login', component: require('@admin/page/Login').default },
    { path: "/html", name: "index", component: require('@admin/components/Layout').default, children: [
      //{ path: "index", name: "index", component: require('@admin/page/SysUserManage').default, meta: ['系统管理', '系统用户管理'] },
      { path: 'sysUserManage', name: "sysUserManage", component: require('@admin/page/SysUserManage').default,meta: ['系统管理', '系统用户管理'],},
      { path: 'roleManage', name: "roleManage", component: require('@admin/page/roleManageWrapper').default,meta: ['系统管理', '角色管理'],},
      { path: 'codeManage', name: "codeManage", component: require('@admin/page/CodeManage').default,meta: ['系统管理', '码值管理'],},
      { path: 'menuManage', name: "menuManage", component: require('@admin/page/menuManage').default,meta: ['系统管理', '菜单管理'],},
      { path: 'riskProduct', name: 'riskProduct', component: require('@admin/page/riskProductWrapper').default,meta: ['产品管理', '风控产品'],},
      { path: 'riskIndex', name: 'riskIndex', component: require('@admin/page/riskIndex').default,meta: ['产品管理', '指标管理'],},
      { path: 'orgRecharge', name: 'orgRecharge' ,component: require('@admin/page/orgRecharge').default,meta: ['计费管理', '机构充值'],},
      { path: 'consumeDetail', name: 'consumeDetail', component: require('@admin/page/consumeDetail').default,meta: ['计费管理', '消费明细'],},
      { path: 'orgManage', name: 'orgManage', component: require('@admin/page/orgManageWrapper').default,meta: ['客户管理', '机构管理'],},
      { path: 'accManage', name: 'accManage', component: require('@admin/page/accManage').default,meta: ['客户管理', '账户管理'],},
      { path: 'log', name: "log", component: require('@admin/page/log').default,meta: ['日志管理', '执行日志'],},
      { path: 'operatelog', name: "operatelog", component: require('@admin/page/operatelog').default,meta: ['日志管理', '操作日志'],},      
      { path: "unauthorized", name: "unauthorized", component: require('@admin/page/NotFound').default },
      { path: "*", name: "404", component: require('@admin/page/NotFound').default }
    ]},
    { path: "/index", redirect: { name: "index"} },
    { path: "/", redirect: { name: "index"} },
    { path: "*", redirect: {name: '404'} }
  ]
})

const perms = ['404', 'login', 'unauthorized', 'index', 'account']
const roles = []
router.beforeEach(async function (to, from, next) {
  await beforeEach({to, from, next, router, perms, roles})
})

export default router



