<template>
    <div class="sysUserManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
            <self-grid ref="grid" url="/api/user/queryUser" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="用户姓名">
                            <el-input placeholder="用户姓名" v-model="form.userName"></el-input>
                        </el-form-item>
                        <el-form-item label="账号：">
                            <el-input placeholder="账号" v-model="form.loginId"></el-input>
                        </el-form-item>
                        <el-form-item label="用户角色：">
                            <el-select v-model="form.roleName" clearable placeholder="请选择">
                                <el-option v-for="item in roleNames" :key="item.roleId" :label="item.roleName" :value="item.roleName"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="状态：">
                            <el-select v-model="form.status" clearable placeholder="请选择">
                                <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="loginId" label="账号"/>
                <el-table-column prop="userName" label="姓名"/>
                <el-table-column prop="roleNames" label="角色"/>
                <el-table-column prop="status" label="状态">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column prop="loginTime" label="上次登录时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.loginTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="{row, index}">
                        <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                        <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-bianji1" @click.stop="editSysUser(row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content="重置密码"  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-zhongzhimima" @click.stop="resetSysUserPwd(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog title="添加账户" v-model="addSysUserFormVisible" @close="closeAddSysUserForm">
            <template slot-scope="{close}">
                <add-sys-user-form :close="close"/>
            </template>
        </self-dialog>
        <!-- 编辑dialog -->
        <self-dialog ref="editSysUserForm" title="修改系统用户" v-model="editSysUserFormVisible" @close="closeEditSysUserForm">
            <template slot-scope="{close, params}">
                <edit-sys-user-form :close="close" :params="params"/>
            </template>
        </self-dialog>
        
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addSysUserForm from '@admin/page/module/addSysUserForm'
import editSysUserForm from '@admin/page/module/editSysUserForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";

//码表
require('@/store/cache').cacheCodes('userType', "status")
@Component
@InjectComponents({ BreadCrumb,addSysUserForm,editSysUserForm })
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    addSysUserFormVisible = false
    editSysUserFormVisible = false
    resetSysUserPwFormVisible = false
    pars ={}
    queryForm = {}
    states = null
    roleNames = []
    async queryAllRole(){
        // 查询所有角色
        const rsp = await this.$helper.fetch("/api/user/queryAllRole").send()
        if (!rsp.success) return
        this.roleNames = rsp.data.list
    }
    // 添加
    add() {
        this.addSysUserFormVisible =  true
    }
    closeAddSysUserForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 启用或禁用
    async changStatus(row){
        const userId = row.userId
        const rsp = await this.$helper.fetch("/api/user/deleteUser").setParam("userId",userId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '提示',message:  rsp.message,type: 'success',duration:4000});
    }
    //编辑
    editSysUser(row){
    if(row.status =='00001002'){
        this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
        return
    }
        this.editSysUserFormVisible =  true
        this.$refs.editSysUserForm.setParams(row)
    }
    closeEditSysUserForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    //重置密码
    async resetSysUserPwd(row){
        if(row.status =='00001002'){
            this.$notify({title: '提示',message:  '已失效，若需进行当前操作，请启用',type: 'error',duration:4000});
            return
        }
        await this.$confirm('此操作将重置该用户密码, 是否继续?', '重置密码')
        const rsp = await this.$helper.fetch("/api/user/restPassWord").setParam("userId",row.userId).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
         await this.$alert('初始密码为123456', '重置密码成功')
       // this.$message({message: rsp.message, type: 'success'});
    }
    @Lifecycle created() {
        this.queryAllRole()
    }
}
</script>


