<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="账号:" prop="loginId">
      <div>{{form.loginId}}</div>
    </el-form-item>
    <el-form-item label="用户姓名:" prop="userName">
      <el-input v-model="form.userName" auto-complete="off"  placeholder="请输入姓名"></el-input>
    </el-form-item>
    <el-form-item label="用户角色:"  prop="roleIds">
      <el-select v-model="form.roleIds" multiple clearable placeholder="请选择">
        <el-option v-for="(item, index) in allRole" :key="index" :label="item.roleName" :value="item.roleId"/>
      </el-select>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
  @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
  submitLoading =false
  form = {loginId: '', userName: '',roleIds:[]}
  // 查询所有角色
  allRole = []
  roleIds = []
  async queryAllRole(){
      const rsp = await this.$helper.fetch("/api/user/queryAllRole").send()
      if (!rsp.success) return
      this.allRole = rsp.data.list
  }
  @Lifecycle created() {
    this.form = {...this.form, ...this.params}
    this.form.roleIds = this.params.roleIds.split(",")
    this.queryAllRole()
  }
  rules = {
    loginId: [
      {trigger: "blur", required: true, message: '请输入帐号'},
      {trigger: 'blur', max: 36, message: '长度不得超过36个字符' },
      {whitespace: true, message: '不能包含空格'},
       {trigger: "blur,change", validator: (rule, value, callback) => {
          if (value.match("^[0-9a-zA-Z]+$")) return callback()
          return callback('只能是字母或数字')
      }}
    ],
    userName: [
      {trigger: "blur", required: true, message: '请输入姓名'},
      {trigger: 'blur', max: 32, message: '长度不得超过32个字符' },
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/^[\u4e00-\u9fa5]+$/
          if (regex.test(value)) return callback()
          return callback(new Error(`姓名只能是汉字`))
      }}
    ],
    roleIds: [
      {type: 'array',trigger: 'change', required: true, message: '请至少选择一个用户角色'}
    ]
  }
  async submit() {
    const thisForm = this.form
    const objRow = {
      userId: thisForm['userId'],
      userName: thisForm['userName'],
      roleIds: thisForm['roleIds']
    }
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading = true
      const rsp = await this.$helper.fetch('/api/user/updateUser').setParams(objRow).send()
      this.submitLoading =false
      if (!rsp.success) return rsp.success
      this.$notify({title: '成功',message:  '编辑成功',type: 'success',duration:4000});

      this.close()
    }
  }
</script>




