<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="机构名称:" prop="orgName">
        <el-input v-model="form.orgName" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="机构地址:" prop="orgAddr">
        <el-input v-model="form.orgAddr" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="行业：" prop="orgIndustryType">
        <el-select v-model="form.orgIndustryType" clearable placeholder="请选择">
            <el-option v-for="(item, index) in codeStorage.dataSource('orgIndustryType')" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
    </el-form-item>
    <el-form-item label="电话号码:" prop="orgTel">
        <el-input v-model="form.orgTel" auto-complete="off" ></el-input>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
require('@/store/cache').cacheCodes('userType', "orgIndustryType")
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @Prop({type: Function, required: true, default: null}) close
  @State(state => state.cache.codeStorage) codeStorage
  submitLoading =false
  form = {orgName: '', orgAddr: '',orgIndustryType:'',orgTel:''}
  rules = {
    orgName: [
      {trigger: "blur", required: true, message: '请输入机构名称'},
      {trigger: 'blur', max: 32, message: '长度不得超过32个字符' },
      {whitespace: true, message: '不能包含空格'}
    ],
    orgAddr: [
      {trigger: "blur", required: true, message: '请输入机构地址'},
      {trigger: 'blur', max: 64, message: '长度不得超过64个字符' },
      {whitespace: true, message: '不能包含空格'}
    ],
    orgIndustryType: [
      {trigger: 'change', required: true, message: '请至少选择一个行业'}
    ],
    orgTel: [
      {trigger: "blur", required: true, message: '请输入电话号码'},
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur, change", validator: (rule, value, callback) => {
        var regExp  = /^((\d{11})|^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$)/;
          if (regExp.test(value)) return callback()
         return callback(new Error('请输入有效的手机号码或座机号'))
        }
     }
    ]
  }
  async submit() {
    const saveOrgObj = {tBusiOrg: this.form}
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading = true
    const rsp = await this.$helper.fetch('/api/busiOrg/saveOrg').setParams(saveOrgObj).send()
    this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '成功',message:  '添加成功',type: 'success',duration:4000});
    this.close()
  }
}
</script>




