<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <org-manage ref="orgManage" @open="open"/>
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
        <org-product-relevance ref="productRelevanceForm" @close="close" :autoload="false"/>
    </template>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import orgManage from './orgManage'
import orgProductRelevance from './orgProductRelevance'
@Component
@InjectComponents({orgManage,orgProductRelevance})
export default class ModifyLoginPwdForm extends Vue{
    visible = true
    //获取accmanage中的emit传过来的参数
    open(params) { 
      console.log("第一个页面的值",params)
        this.visible = false
      this.$nextTick(() => {
        this.$refs.productRelevanceForm.setParams(params)
      })
    }
    close(data) {
        this.visible = true
    }
}
</script>




