<template>
    <div class="roleManage contentBox">
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/busiOrg/queryOrgNoProd" v-model="queryForm" :params="() => ({'orgId': form.orgId})">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="产品名称" class="prodName">
                            <el-input v-model="form.prodName" placeholder="产品名称"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="primary" @click="add">添加</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="prodId" label="产品ID"> </el-table-column>
                <el-table-column prop="prodName" label="产品名称"></el-table-column>
                <el-table-column prop="costAmt" label="费用"></el-table-column>
                <el-table-column prop="mngType" label="风控级别">
                    <template slot-scope="{row}">{{ codeStorage.get('mngType', row.mngType) }}</template>
                </el-table-column>
                <el-table-column prop="updateTime" label="上架时间">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.updateTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="状态">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//码表
require('@/store/cache').cacheCodes('statues', "indicatorType","mngType")
@Component
export default class SysUserManage extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
    @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
    @Prop({type: Boolean, required: false, default: true}) autoload
    queryForm = {}
    form = {'orgId':'','prodName':''}
    // 添加
    async add(){
        const rows = this.$refs.grid.selectedRows
        if(rows.length ==0) 
        return this.$notify({message:  '请至少选择一条数据',type: 'error',duration:4000});
        const prodIds = []
        for(var i in rows){prodIds.push(rows[i].prodId)}
        const setObj = {"prodIds":prodIds,"orgId":this.form.orgId}
        const rsp = await this.$helper.fetch("/api/busiOrg/saveOrgProd").setParams(setObj).send()
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
        this.close()
    }
    @Lifecycle created() {
        this.form = {...this.form, ...this.params}
    }
}
</script>



