<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="机构名称:" prop="orgId">
        <el-select v-model="form.orgId" clearable placeholder="请选择">
          <el-option v-for="item in allOrgList" :key="item.orgId" :label="item.orgName" :value="item.orgId"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item label="充值金额:" prop="amt">
        <el-input v-model="form.amt" auto-complete="off" ></el-input>
    </el-form-item>
    <el-form-item label="类型：" prop="extAmt">
        <el-select v-model="form.extAmt" clearable placeholder="请选择">
          <template slot-scope="{row}">
            <el-option v-for="(item, index) in setBillType" :key="index" :label="item.label" :value="item.value"/>
          </template>
        </el-select>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
require('@/store/cache').cacheCodes('userType', "billType")
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @State(state => state.cache.codeStorage) codeStorage
  @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
  @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
  submitLoading =false
  allOrgList = []
  setBillType = []
  async queryAllOrg(){
      // 查询所有机构名称
      const rsp1 = await this.$helper.fetch("/api/busiCust/queryOrgs").send()
      if (!rsp1.success) return
      console.log(rsp1.data.list)
      this.allOrgList = rsp1.data.list
  }
  form = {orgId: '', amt: '',extAmt:''}
  rules = {
    orgId: [
        {trigger: 'change', required: true, message: '请选择机构名称'},
    ],
    amt: [
      {trigger: "blur", required: true, message: '请输入充值金额'},
      {trigger: 'blur, change', max: 9, message: '长度不得超过9个字符' },
      {trigger: "blur, change", validator: (rule, value, callback) => {
             var isNum=/^\d+(\.\d{0,2})?$/;
            if (isNum.test(value)) return callback()
            return callback(new Error('请输入正确的金额'))
         }}
    ],
    extAmt: [
       {trigger: 'change', required: true, message: '请选择类型'},
    ]
  }
  async submit() {
    const thisForm = this.form
    const objRow = {
      orgId: thisForm['orgId'],
      amt: thisForm['amt'],
      extAmt: thisForm['extAmt']
    }
    const saveOrgObj = {tBusiOrg: objRow}
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//验证
    this.submitLoading =true
    const rsp = await this.$helper.fetch('/api/orgBill/orgRecharge').setParams(objRow).send()
    this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '成功',message:  '添加成功',type: 'success',duration:4000});

    this.close()
  }
  @Lifecycle created() {
      this.queryAllOrg()
      // 过滤充值类型码表，只显示"充值"和"赠送"
      const billTypeArray = this.codeStorage.dataSource('billType')
      for(var i in billTypeArray){ if( billTypeArray[i].value=='10009001' || billTypeArray[i].value=='10009002'){this.setBillType.push(billTypeArray[i])} }
  }
}
</script>




