<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="账号:" >
      <div>{{ form.loginId }}</div>
    </el-form-item>
    <el-form-item label="机构名称:">
      <div>{{ form.orgName }}</div>
    </el-form-item>
    <el-form-item label="姓名:" prop="userName">
        <el-input v-model="form.userName" auto-complete="off" placeholder="请输入姓名"></el-input>
    </el-form-item>
    <el-form-item label="部门:" prop="userDept">
        <el-input v-model="form.userDept" auto-complete="off" placeholder="请输入部门"></el-input>
    </el-form-item>
    <el-form-item label="角色:"  prop="roleId">
        <el-select v-model="form.roleId" clearable placeholder="请选择">
            <el-option v-for="item in allRoleList" :key="item.roleId" :label="item.roleName" :value="item.roleId"></el-option>
        </el-select>
    </el-form-item>
   <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>          
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
require('@/store/cache').cacheCodes('userType', "orgIndustryType")
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @Prop({type: Function, required: true, default: null}) close
  @State(state => state.cache.codeStorage) codeStorage
  @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
  @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
  submitLoading =false
  form = {loginId: '', userName: '',userTel:'',userDept:'',roleId:'',orgId:''}
  allOrgList = []
  allRoleList = []
  async queryAllOrg(){
      // 查询所有机构名称
      const rsp1 = await this.$helper.fetch("/api/busiCust/queryOrgs").send()
      if (!rsp1.success) return
      console.log(rsp1.data.list)
      this.allOrgList = rsp1.data.list
       // 查询所有角色名称
      const rsp2 = await this.$helper.fetch("/api/busiCust/queryOrgRoles").send()
      if (!rsp2.success) return
      this.allRoleList = rsp2.data.list
  }
  rules = {
    loginId: [
      {trigger: "blur", required: true, message: '请输入账号（绑定电话号码）'},
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur, change", validator: (rule, value, callback) => {
        if (value.length == 11 && /[0-9]{11}/.test(value)) return callback()
        return callback(new Error('请输入长度为11位有效的手机号码'))
      }}
    ],
    orgId: [
      {trigger: 'change', required: true, message: '请至少选择机构名称'}
    ],
    userName: [
      {trigger: "blur", required: true, message: '请输入姓名'},
      {trigger: 'blur', max: 32, message: '长度不得超过32个字符' },
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/^[\u4e00-\u9fa5]+$/
          if (regex.test(value)) return callback()
          return callback(new Error(`姓名只能是汉字`))
      }}
    ],
    userDept: [
      {trigger: "blur", required: true, message: '请输入部门'},
      {trigger: 'blur', max: 64, message: '长度不得超过64个字符' },
      {whitespace: true, message: '不能包含空格'},
    ],
     roleId: [
      {trigger: 'change', required: true, message: '请选择角色'}
    ]
  }
  async submit() {
    const thisForm = this.form
    const saveOrgObj = {
      sysUser:{"userName":thisForm['userName'],"userDept":thisForm['userDept'],"userId":thisForm['userId'],"loginId":thisForm['loginId']},
      "roleId":thisForm['roleId'], "custId":thisForm['custId'], "sysUuid":thisForm['sysUuid']
    }
    console.log(saveOrgObj)
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading = true
    const rsp = await this.$helper.fetch('/api/busiCust/editCust').setParams(saveOrgObj).send()
    this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '成功',message:  '编辑成功',type: 'success',duration:4000});

    this.close()
  }
  @Lifecycle created() {
      this.queryAllOrg()
      this.form = {...this.form, ...this.params}
  }
}
</script>




