<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="指标名称:" prop="indicatorName">
      <el-input v-model="form.indicatorName" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="code编码:" prop="indicatorId">
      <el-input v-model="form.indicatorId" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="状态:" prop="status">
      <el-select v-model="form.status" clearable placeholder="请选择">
          <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
      </el-select>
    </el-form-item>
    <el-form-item label="类型:" prop="indicatorType">
        <el-select v-model="form.indicatorType" clearable placeholder="请选择" @change="changeType">
          <el-option v-for="(item, index) in codeStorage.dataSource('indicatorType')" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
    </el-form-item>
    <el-form-item label="业务类型:" prop="indcType">
        <el-select v-model="form.indcType" clearable placeholder="请选择">
          <el-option v-for="(item, index) in codeStorage.dataSource('indcType')" :key="index" :label="item.label" :value="item.value"/>
        </el-select>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right">取 消</el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>          
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
require('@/store/cache').cacheCodes('userType', "indicatorType","indcType")
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @State(state => state.cache.codeStorage) codeStorage
  @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
  @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
  submitLoading =false
  form = {indexType:'',indicatorName: '', indicatorId: '',status:'',indicatorType:'',indcType:''}
  rules = {
    indicatorName: [
      {trigger: "blur", required: true, message: '请输入指标名称'},
      {trigger: 'blur', max: 32, message: '长度不得超过32个字符' },
      {whitespace: true, message: '不能包含空格'}
    ],
    indicatorId: [
      {trigger: "blur", required: true, message: '请输入code编码'},
      {trigger: 'blur', max: 64, message: '长度不得超过64个字符' },
      {whitespace: true, message: '不能包含空格'},
    ],
    status: [
      {trigger: 'change', required: true, message: '请选择指标状态'}
    ],
    indicatorType: [
      {trigger: 'change', required: true, message: '请选择指标类型'}
    ],
    indcType: [
       {trigger: 'change', required: false, message: '请选择业务类型'}
    ]
  }
  async changeType(){
    if(this.form['indicatorType'] == '10008002'){//基础指标，必须选择业务类型,反之则选填 
      this.rules.indcType[0].required = true
    }else{
      this.rules.indcType[0].required = false
    }
  }
  async submit() {
    const thisForm = this.form
    const parentIndcId = this.form.parentIndcId
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    if(!parentIndcId || parentIndcId==""){ thisForm.indexType ='root' }else{ thisForm.indexType ='son' }
    const saveOrgObj = {
      type: thisForm['indexType'],
      tBusiIndicator:{"indicatorId":thisForm['indicatorId'],"indcType":thisForm['indcType'],"indicatorName":thisForm['indicatorName'],"indicatorType":thisForm['indicatorType'],"status":thisForm['status'],"parentIndcId":thisForm['parentIndcId']}
    }
    this.submitLoading =true
    const rsp = await this.$helper.fetch('/api/indc/saveIndi').setParams(saveOrgObj).send()
    this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '成功',message:  '添加成功',type: 'success',duration:4000});
    this.close()
  }
  @Lifecycle created() {
      this.form = {...this.form, ...this.params}
  }
}
</script>




