<template>
    <div class="roleManage contentBox">
        <div class="breadCrumb">
            <el-breadcrumb separator="》">
                <el-breadcrumb-item>产品管理</el-breadcrumb-item>
                <el-breadcrumb-item >风控产品</el-breadcrumb-item>
                <el-breadcrumb-item>关联指标</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container whiteBg prodIndexDialog">
             <self-grid ref="grid" url="/api/prod/getIndicatorOnProd" :checkBox="false" v-model="queryForm" :autoload="autoload" :params="() => ({prodId: indexParams.prodId})">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="指标名称">
                            <el-input v-model="form.indicName" placeholder="指标名称"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query">查询</el-button>
                            <el-button type="danger" @click="couple(form)">关联</el-button>
                             <el-button type="info" @click="() => $emit('close')" >返回</el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="indicatorName" label="指标名称"> </el-table-column>
                <el-table-column prop="indicatorId" label="code编码"></el-table-column>
                <el-table-column prop="indicatorType" label="指标类型">
                    <template slot-scope="{row}">{{ codeStorage.get('indicatorType', row.indicatorType) }}</template>
                </el-table-column>
                <el-table-column prop="indcType" label="业务类型" width="100">
                    <template slot-scope="{row}">{{ codeStorage.get('indcType', row.indcType) }}</template>
                </el-table-column>
                <el-table-column prop="nstatus" label="状态" width="100">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.nstatus) }}</template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template slot-scope="{row, index}">
                        <template v-if="row.nstatus != '00001001'">
                            <el-tooltip class="item" effect="light" content="删除"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-shanchu" @click.stop="del(row)" disabled></el-button>
                            </el-tooltip>
                        </template>
                        <template v-if="row.nstatus != '00001002'">
                            <el-tooltip class="item" effect="light" content="删除"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-shanchu" @click.stop="del(row)"></el-button>
                            </el-tooltip>
                        </template>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- 添加dialog -->
        <self-dialog ref="coupleProdIndexForm" title="风控产品添加指标" v-model="coupleProdIndexFormVisible" @close="closeCoupleProdIndexForm" class="indexDialog">
            <template slot-scope="{close, params}">
                <couple-prod-index-form :close="close" :params="params"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import coupleProdIndexForm from '@admin/page/module/coupleProdIndexForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
require('@/store/cache').cacheCodes('userType', "status","indicatorType","indcTypes")
@Component
@InjectComponents({ BreadCrumb,coupleProdIndexForm })
export default class SysUserManage extends Vue {
    @State(state => state.cache.codeStorage) codeStorage
    @Prop({type: Boolean, required: false, default: true}) autoload
    coupleProdIndexFormVisible = false
    queryForm = {}
    states = null
    indexParams = {}
    // 关联
    couple(params) {
        this.coupleProdIndexFormVisible =  true
        this.$refs.coupleProdIndexForm.setParams(params)
    }
    closeCoupleProdIndexForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }
    // 删除
    async del(row){
        const sysUuid = row.sysUuid
        console.log(row)
        const rsp = await this.$helper.fetch("/api/prod/alterProIndicatorStatus").setParam("sysUuid",sysUuid).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:   rsp.message,type: 'success',duration:4000});
    }
    setParams(params = {}){
        const {prodId = ''} = params
        this.indexParams = params
        this.queryForm = {...this.queryForm,prodId}
        this.$refs.grid.setDefaultParams({prodId}).load()//内置一个值
    }
}
</script>
<style scoped>

</style>



