<template>
    <div class="codeManage riskIndex contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
            <div class="indexLeft" style="padding-right:10px">
                <div class="halfTitle">指标结构</div>
                <div class="halfButton">
                    <el-button type="success" @click="addRootIndex">添加根指标</el-button>
                    <el-button type="primary" @click="addSonIndex">添加下级指标</el-button>
                </div>
                <el-tree ref="tree" :expand-on-click-node="false" :data="allMenu" @node-click="nodeClick" node-key="indicatorId" highlight-current :props="{label: 'indicatorName'}"></el-tree>
            </div>
            <div class="indexRight">
                <div class="halfTitle">指标管理</div>
                <self-grid ref="grid" url="/api/indc/getIndiList" v-model="queryForm" :multi="false" :checkBox="false">
                    <template slot="query" slot-scope="{form, query, reset}">
                        <el-form :inline="true" :model="form" class="demo-form-inline">
                            <el-form-item label="指标名称：">
                                <el-input placeholder="指标名称" v-model="form.indicatorName"></el-input>
                            </el-form-item>
                            <el-form-item label="插入时间：">
                                <el-date-picker @change="value => form.insertTime = value" v-model="form.insertTime_" type="date" placeholder="选择日期"></el-date-picker>
                            </el-form-item>
                            <el-form-item class="buttonTeam halfButton">
                                <el-button type="warning" @click="query">查询</el-button>
                            </el-form-item>
                        </el-form>
                    </template>
                    <el-table-column prop="indicatorName" label="指标名称"></el-table-column>
                    <el-table-column prop="indicatorId" label="code编码"></el-table-column>
                    <el-table-column prop="indicatorType" label="类型" width="80px">
                        <template slot-scope="{row}">{{ codeStorage.get('indicatorType', row.indicatorType) }}</template>
                    </el-table-column>
                    <el-table-column prop="indcType" label="业务类型" width="80px">
                        <template slot-scope="{row}">{{ codeStorage.get('indcType', row.indcType) }}</template>
                    </el-table-column>
                    <el-table-column prop="status" label="状态" width="60">
                        <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                    </el-table-column>
                    <el-table-column prop="userName" label="操作人员"> </el-table-column>
                    <el-table-column prop="insertTime" label="插入时间" width="100px">
                        <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
                    </el-table-column>
                    <el-table-column label="操作" width="120">
                        <template slot-scope="{row, index}">
                            <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="启用" off-text="禁用" @change="changStatus(row)"></el-switch>
                            <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-bianji1" @click.stop="editRiskIndex(row)"></el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </self-grid>
            </div>
             <!-- 新增dialog -->
            <self-dialog ref="addRiskIndexForm" title="添加指标" v-model="addRiskIndexFormVisible" @close="closeAddRiskIndexForm">
                <template slot-scope="{close, params}">
                    <add-risk-index-form :close="close" :params="params"/>
                </template>
            </self-dialog>
            <!-- 编辑dialog -->
            <self-dialog ref="editRiskIndexForm" title="修改指标" v-model="editRiskIndexFormVisible" @close="closeEditRiskIndexForm">
                <template slot-scope="{close, params}">
                    <edit-risk-index-form :close="close" :params="params"/>
                </template>
            </self-dialog>
            <div style="clear:both"></div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import editRiskIndexForm from '@admin/page/module/editRiskIndexForm'
import addRiskIndexForm from '@admin/page/module/addRiskIndexForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
editRiskIndexForm
//码表
require('@/store/cache').cacheCodes('userType', "status", "indicatorType","indcType")
@Component
@InjectComponents({ BreadCrumb,editRiskIndexForm,addRiskIndexForm })
export default class riskIndex extends Vue {
    //使用码表
    @State(state => state.cache.codeStorage) codeStorage
    @Lifecycle created() {
        this.queryMenus()
    }
    editRiskIndexFormVisible = false
    addRiskIndexFormVisible = false
    queryForm = {}
    allMenu=[]
    selectIndex=null
    // 查询指标列表
    async queryMenus(){
        const rsp = await this.$helper.fetch("/api/indc/getTreeList").send()
        if (!rsp.success) return
        this.allMenu = this.$helper.tools.toTreeArray(rsp.data.list, 'indicatorId', 'parentIndcId')
    }
    // 启用或禁用
    async changStatus(row){
        const id = row.indicatorId
        const rsp = await this.$helper.fetch("/api/indc/alterIndiStatus").setParam("id",id).send()
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        if (!rsp.success) return
        this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
        this.queryMenus()
    }
    // 点击树节点，右边grid重新加载
    async nodeClick(data,node){
        this.selectIndex = data
        const setObj = {"id":data.indicatorId}
        this.$refs.grid.setDefaultParams(setObj).load({"pageNo":1});//重新加载列表
    }
    // 添加根指标
    async addRootIndex(){
        this.addRiskIndexFormVisible = true
        const setObj = {"parentIndcId":''}
        this.$refs.addRiskIndexForm.setParams(setObj)
    }
    // 添加下级指标
    async addSonIndex(){
        if( !this.selectIndex ) 
        return this.$notify({title: '成功',message:  '请选择一个指标',type: 'warning',duration:4000});
        const setObj = {"parentIndcId":this.selectIndex.indicatorId}
        this.addRiskIndexFormVisible = true
        this.$refs.addRiskIndexForm.setParams(setObj)
    }
    async closeAddRiskIndexForm(){
        this.queryMenus()
        this.$refs.grid.load({"pageNo":1})//重新加载列表
    }
    // 编辑
    editRiskIndex(row){
         if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '当前指标已失效,请启用',type: 'error',duration:4000});
          return
    }
        this.editRiskIndexFormVisible =  true
        this.$refs.editRiskIndexForm.setParams(row)
    }
    async closeEditRiskIndexForm(){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
        this.queryMenus()
    }
}
</script>
<style scoped>
.codeManage .el-table th > .cell {
  width: 100px;
}
</style>


